@import url(/css/vendor/literallycanvas/literallycanvas.css);
@import url(/css/vendor/sweetalert/sweetalert.css);
h4.page-title {
  margin-top: 0; }

.tile.form {
  text-align: left; }

.checkbox, .radio {
  text-align: left; }

.checkbox input[type=checkbox].custom-checkbox, .radio input[type=checkbox].custom-checkbox, .checkbox input[type=radio].custom-radio, .radio input[type=radio].custom-radio {
  z-index: 2; }

.area {
  background-color: #dce6e9;
  border-radius: 6px;
  padding: 14px;
  margin: 20px 0; }

.hide {
  position: absolute !important;
  top: -9999px !important;
  left: -9999px !important; }

/*Bootstrap Override:;*/
.list-group-item.active, .list-group-item.active:hover, .list-group-item.active:focus {
  background-color: #1abc9c;
  border-color: #1abc9c; }

